import {
	get
} from "../utils/request.js"
import {
	queryGarbagUrl
} from "../utils/urls.js"

export default {
	props: {
		inputHint: {
			type: String,
			default: '请输入垃圾名称'
		},
		inputType: {
			type: String,
			default: 'text'
		},
		isPassword: {
			type: Boolean,
			default: false
		},
		confirmType: {
			type: String,
			default: 'done'
		}
	},
	data() {
		return {
			isClearShow: false, // 显示删除按钮
			inputValue: '', // 输入框的内容
			resultList: [] // 搜索数据
		}
	},
	methods: {
		// 输入框事件
		inputListener(e) {
			const {
				value
			} = e.mp.detail
			this.inputValue = value
			if (value === null || value === undefined || value.length === 0) {
				this.isClearShow = false
			} else {
				this.isClearShow = true
			}
		},
		// 输入框确认事件
		inputConfirm(e) {
			const {
				value
			} = e.mp.detail
			this.inputValue = value
			if (value) {
				this.isChinese(value, this.getResultList)
			}
		},
		// 获取数据
		getResultList(name) {
			get({
				url: queryGarbagUrl,
				params: {
					name
				}
			}).then(res => {
				console.log(res)
				if (res.data) {
					this.resultList = res.data
				}
			})
		},
		// 跳转到详情
		bindNavigate(e) {
			this.isChinese(e, this.navigateToDetail)
		},
		navigateToDetail(key) {
			this.clearTap()
			uni.navigateTo({
				url: `/pages/detail/detail?key=${key}`
			})
		},
		// 清除输入框
		clearTap() {
			this.isClearShow = false
			this.inputValue = ''
			this.resultList = []
		},
		// 判断是否是中文
		isChinese(key, func) {
			let reg = /[\u4E00-\u9FA5\uF900-\uFA2D]/
			let isChinese = reg.test(key)
			if (isChinese) {
				// eslint-disable-next-line no-unused-expressions
				func(key)
			} else {
				uni.showModal({
					content: '请输入中文',
					showCancel: false
				})
			}
		}
	},

}
